
import Cell from 'vux/src/components/cell/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
components: {
Group,
Cell,
Divider,
XButton,
XDialog,
XSwitch,
Alert
},
directives: {
TransferDom
},
data () {
return {




msg: 'Hello World!',
showHideOnBlur: false,
comment:'',
}

},
methods:{
copyUrl:function(){
var Url=document.getElementById("data").innerText;
var oInput = document.createElement('input');
oInput.value = Url;
document.body.appendChild(oInput);
oInput.select(); 
document.execCommand("Copy"); 
oInput.className = 'oInput';
oInput.style.display='none';
this.$vux.toast.text('复制成功', 'top')
},
getParam: function() {
var u = window.location.search;
var  len = u.length;
return u.substring(3, len)
},
getComment:function(){
var u = this.getParam();
console.log(u)
this.$http.get('/api/user/'+u).then(({data}) => {
if(data.data.status == 1){
this.comment = data.data.data
this.showHideOnBlur = true;

}else{
this.$vux.alert.show({
title: '错误信息！',
content: data.data.data,
})
setTimeout(() => {
this.$vux.alert.hide()
}, 3000)
}

})
},

},
created () {
}
}



